.epc-main-img{
  background-color: var(--color-blue);
}
.epc-header{
  background-color: var(--color-blue);
}
.epc-header h2{
  color: var(--color-mainyellow, #FFD50C);
text-align: center;
font-size: 30px;
font-family: 'Poppins';
font-weight: 600;
line-height: 100%; 
letter-spacing: -0.78px;
}

.eng-header h3,
.pro-header h3,
.cons-header h3{
font-family: Poppins;
font-size: 30px;
font-style: normal;
font-weight: 600;
line-height: 150%;
letter-spacing: -0.9px;
}
.pro-header h3{
color: var(--color-blue);
}
.epc-data span{
color: var(--color-white);
font-family: 'Poppins';
font-size: 18px;
font-weight: 100;
line-height: 150%;
letter-spacing: -0.54px;
opacity: 0.9;
}
.epc-eng ul span,
.cons-data ul{
font-family: 'Poppins';
font-size: 18px;
font-style: normal;
font-weight: 300;
line-height: 150%;
letter-spacing: -0.54px;
}

.pro-data ul{
color: var(--color-blue);
font-family:'Poppins';
font-size: 18px;
font-style: normal;
font-weight: 300;
line-height: 150%; 
letter-spacing: -0.54px;
}

      
.line-six-animation.show{
opacity: 1;
  transform: translateY(0);
}




.line-six-animation {
    transition: opacity 1s ease-out 2.5s , transform 1s ease-out 2.5s;
    transform: translateY(-100%); 
    display: block;
    opacity: 0;
  }


.epc-data,
.cons-data {
  animation: slideInFromRight 0.5s ; 

}

@keyframes slideInFromRight {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0%); 
  }
}

    
.line-seven-animation.show{
opacity: 1;
  transform: translateY(0);
}




.line-seven-animation {
  opacity: 0;
    transition: opacity 1s ease-out , transform 1s ease-out  ;
    transform: translateY(100%); 
  }

.pro-data {
  animation: slideInFromLeft 1s ease-out ; 
}

@keyframes slideInFromLeft {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0); 
  }
}


.cons-data ,
.epc-data{
  opacity: 0; 
  transition: opacity 1s ease-out , transform 1s ease-out ;
  transform: translateX(100%); 
}


.cons-data.show ,
.epc-data.show {
  opacity: 1; 
  transform: translateX(0%);
}
.pro-data {
  opacity: 0; 
  transition: opacity 1s ease, transform 1s ease ; 
  transform: translateX(-100%); 
}

.pro-data.show{
  opacity: 1;
  transform: translateX(0%);
}


.marquee-wrapper {

  display: flex;
  align-items: center;
  height: 100px;
  overflow: hidden;
  
}


.marquee-text {
 white-space: nowrap;
 margin: 0 2em;

 

}
.marquee-text div{
display: flex;
}

.marquee-text p{
margin-bottom: 0;
font-size: 26px;
font-style: normal;
font-weight: 500;
line-height: 100%; 
letter-spacing: -0.78px;
color:var(--color-mainyellow);
font-family: 'Poppins';
}

.move-right{
animation: marquee 20s infinite linear;
}
@keyframes marquee {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-25%);
  }
}
