.app-services{

  transition: transform 0.5s ease ;
}

.app-our-services{
  background-color: var(--color-blue);
}

.app-services h2{
color: var(--color-mainyellow, #FFD50C);
text-align: center;
font-family: 'Poppins';
font-size: 30px;
font-weight: 600;
line-height: 100%;
letter-spacing: -0.78px;

}
.app-our-services h3{
  width: 196px;
  height: 73px;
  color: var(--color-mainyellow);
font-size: 26px;
font-style: normal;
font-weight: 500;
line-height: 100%; 
letter-spacing: -0.78px;
text-align: center;
}

.our-services-electro,
.our-services-operation{
  background-color: var(--color-blue);
  
}
.electro-data h4,
.operation-data h4{
  color: var(--color-white, #FBFCFF);
  font-size: 30px;
  font-weight: 600;

}
.electro-data p,
.operation-data p{
  color: #FFF;
font-size: 18px;
font-weight: 300;
line-height: 150%; 
letter-spacing: -0.54px;
width: 634px;
height: 137px;

}
.our-services-auto,
.our-services-main{
  background-color: var(--color-white, #FBFCFF);
}
.auto-data h4,
.main-data h4{
  color: var(--color-blue, #003566);
  font-size: 30px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  letter-spacing: -0.9px;
  text-align: center;
}
.auto-data p,
.main-data p{
  color: #013567;
font-size: 18px;
font-style: normal;
font-weight: 300;
font-family: 'Poppins';
line-height: 150%; 
letter-spacing: -0.54px;
}
.main-data p{
opacity: 0.85;
}

.vertical-lines.show{
opacity: 1;
transform: translateY(0);
}




.vertical-lines {
  display: block;
  opacity: 0;
  transition: opacity 1s ease-out 2.5s, transform 1s ease-out 2.5s ;
  transform: translateY(-100%);

}


.line-between {
  opacity: 0;
  transition: opacity 1s ease-out 1s,transform 1s ease-out 1s;
  transform: translateY(-50%); 
}

.line-between.show  {
  opacity: 1;
  transform:translateY(0%);
}


.electro-data ,
.operation-data {
  opacity: 0; 
  transition: opacity 1s ease-out 0.5s , transform 1s ease-out 0.5s ;
  transform: translateX(100%); 
}

.electro-data.show ,
.operation-data.show{
  opacity: 1; 
  transform: translateX(0%);
}
.auto-data ,
.main-data {
  opacity: 0; 
  transition: opacity 1s ease-out  , transform 1s ease-out  ; 
  transform: translateX(-100%); 
}

.auto-data.show ,
.main-data.show
{
  opacity: 1;
  transform: translateX(0%);
}
.line-animation  {

  animation-name: slideInFromBottom 1s ease-out ; 
}

@keyframes slideInFromBottom {
 
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0); 
  }
}






.marquee-wrapper {

  display: flex;
  align-items: center;
  height: 100px;
  overflow: hidden;
  
}


.marquee-text {
 white-space: nowrap;
 margin: 0 2em;



}
.marquee-text div{
display: flex;
gap: 8px;
}

.marquee-text p{
margin-bottom: 0;
font-size: 26px;
font-weight: 500;
line-height: 100%; 
font-style: normal;
letter-spacing: -0.78px;
font-family: 'Poppins';
color: var(--color-mainyellow);

}

.move-right{
animation: marquee 1s infinite linear;
}
@keyframes marquee {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-20%);
  }
}

.line-four-animation.show{
opacity: 1;
transform: translateY(0);

}




.line-four-animation {
  transition: opacity 0.3s ease-out  , transform 0.3s ease-out ;
  transform: translateY(100%); 

}
