.our-mission-img,
.our-vision-data ,
.our-team-data {
  opacity: 0; 
  transition: opacity 1s ease, transform 1s ease ;
  transform: translateX(100%); 
}

.our-mission-img.show ,
.our-vision-data.show ,
.our-team-data.show

 {
  opacity: 1; 
  transform: translateX(0%);
}

.our-mission-data ,
.our-vision-img{
  opacity: 0; 
  transition: opacity 1s ease, transform 1s ease ; 
  transform: translateX(-100%); 
}

.our-mission-data.show ,
.our-vision-img.show
{
  opacity: 1;
  transform: translateX(0%);
}

