

@import url('https://fonts.googleapis.com/css?family=Poppins');

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


.poppins-medium {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-style: normal;
}

.poppins-semibold {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-style: normal;
}

.poppins-bold {
  font-family: "Poppins";
  font-weight: 700;
  font-style: normal;
}

.poppins-extrabold {
  font-family: "Poppins";
  font-weight: 800;
  font-style: normal;
}

.poppins-black {
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  font-style: normal;
}





:root{
  --color-gold:#FED60A;
  --color-blue:#003566;
  --color-darkblue:#001D3D;
  --color-yellow:#FFC300;
  --color-mainyellow:#FFD50C;
  --color-white:#FBFCFF;
  --color-mix:linear-gradient(180deg, #003566 3.94%, rgba(0, 53, 102, 0.60) 22.79%, rgba(0, 53, 102, 0.00) 47.5%);
  --main-blue:rgba(0, 53, 102, 1);
  --color-white:rgba(251, 252, 255, 1);
  --font-base:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif

}

*{
  
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: 'Poppins', sans-serif;
}
body{
  background-color: var(--color-blue);
 overflow-x: hidden;
 font-family: 'Poppins',sans-serif;
  
}



.all-navbar{
  background-color:var(--color-blue);
  
}


.app-nav{
  font-size: 18px;
letter-spacing: -0.3px;
opacity: 0.98;
font-weight: 500;
font-family: 'Poppins';



}


.app-footer{
  color: var(--color-yellow);
  background-color: var(--color-blue);
  font-family: 'Poppins';
  font-size: 12px;
font-style: normal;
font-weight: 600;

}
