.app-contact {
    background-color: var(--color-blue);
}
.contact h2{
text-align: center;
font-family: Poppins;
font-size: 30px;
font-style: normal;
font-weight: 600;
line-height: 150%;
letter-spacing: -0.9px;
}
.contact-header p{
    color: var(--color-mainyellow);
font-size: 18px;
font-style: normal;
font-weight: 500;
letter-spacing: -0.7px;
width: 494px;
height: 140px;
justify-content: center;
font-family: 'Poppins';
}
.app-contact-i a{
    color: var(--color-mainyellow);
font-size: 24px;
font-style: normal;
font-weight: 600;
line-height: 150%;
letter-spacing: -0.78px;
font-family: 'Poppins';

}
.contact-header {
    animation: slideInFromRight 1s ease forwards ;
  }
  @keyframes slideInFromLeft {
    0% {
      transform: translateX(-100%);
    }
    100% {
      transform: translateX(0);
    }
  }
.contact-info{
     
        animation: slideInFromRight 1s ease forwards 1s;
      
      }
      .contact-header,
      .contact-info{
        opacity: 0; 
        transition: opacity 1s ease , transform 1s ease ;
        transform: translateX(100%); 
      }
      
      .contact-header.show,
      .contact-info.show
      
       {
        opacity: 1; 
        transform: translateX(0%);
      }
      
    
      .contact-info {
        opacity: 0; 
        transition: opacity 1s ease 1s, transform 1s ease 1s;
        transform: translateX(100%); 
      }
      