
.label,
.ind,
.om-img
{
    background-color: var(--color-blue);
}

 .img-pure{
  transform: rotate(-69.343deg);
  filter: blur(2px);
 } 
 .om-content h2{
  color: var(--color-mainyellow);
text-align: center;
font-size: 30px;
font-family: 'Poppins';
font-weight: 600;
line-height: 100%; 
letter-spacing: -0.78px;
}
.operation-data h3 ,
.pred-header h3,
.ind-data h3{
  font-family: 'Poppins';
font-size: 30px;
font-style: normal;
font-weight: 600;
line-height: 150%; 
letter-spacing:-0.9px;
}
.pred-header h3,
.pred-data ul
  {
    color:var(--color-blue)
  }
.operation-data ul,
.pred-data ul ,
.ind-data ul{
  font-family: 'Poppins';
font-size: 18px;
font-style: normal;
font-weight: 300;
line-height: 150%; 
letter-spacing: -0.54px;
}
  .ind-data,
  .operation-data {
    opacity: 0; 
    transition: opacity 1s ease, transform 1s ease ;
    transform: translateX(100%); 
  }
  
  .ind-data.show ,
  .operation-data.show{
    opacity: 1; 
    transform: translateX(0%);
  }
  .pred-data{
    opacity: 0; 
    transition: opacity 1s ease, transform 1s ease ; 
    transform: translateX(-100%); 
  }
  
  .pred-data.show {
    opacity: 1;
    transform: translateX(0%);
  }


.half-circle{
  filter: blur(2px);
  transform: rotate(-69.343deg);
}
.marquee-wrapper {

  display: flex;
  align-items: center;
  height: 100px;
  overflow: hidden;
  
}


.marquee-text {
 white-space: nowrap;
 margin: 0 2em;



}
.marquee-text div{
display: flex;
}

.marquee-text p{
margin-bottom: 0;
font-size: 26px;
font-style: normal;
font-weight: 500;
line-height: 100%; 
letter-spacing: -0.78px;
font-family: 'Poppins';
color: var(--color-mainyellow);
}

.move-right{
animation: marquee 20s infinite linear;
}
@keyframes marquee {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-25%);
  }
}
.line-seven-animation.show{

  transform: translateY(0);
}




.line-seven-animation {
    transition: opacity 0.2s ease , transform 0.2s ease  ;
    transform: translateY(100%); 
  }


.line-six-animation.show{
opacity: 1;
  transform: translateY(0);
}




.line-six-animation {
  display: block;
  opacity: 0;
    transition: opacity 1s ease 2.5s, transform 1s ease 2.5s;
    transform: translateY(-100%); 
  }
